.App {
  margin: 0;
  box-sizing: border-box;
  position: relative;
}

.left,
.right {
  text-decoration: none;
  margin: 0.5rem;
  font-size: 40px;
  color: #fff;
  z-index: 10;
}

.left {
  position: absolute;
  top: 50%;
  left: 0;
}

.right {
  position: absolute;
  top: 50%;
  right: 0;
}

.header-1 {
  display: grid;
  grid-template-columns: 1fr 300px;
  background: #ffffff;
}

.header-1 div a {
  background: #ef9322;
  color: #fff;
  width: 80px;
  padding: 0.5rem;
  text-decoration: none;
}
.header-1 div {
  padding-bottom: 20px;
}

.header-1 ul li {
  display: inline-block;
  padding: 0.5rem;
}

.header-2 {
  display: grid;
  grid-template-columns: 1fr 150px;
  background: #333;
  color: #fff;
  height: 50px;
  padding-top: 1.2rem;
  padding-left: 0.5rem;
}

.header-2 a {
  text-decoration: none;
  padding: 0.5rem;
  color: #fff;
}

.header-2 .partner {
  border: #fff 1px solid;
  -webkit-transform: skew(-30deg);
          transform: skew(-30deg);
}

.header-2 .clubs {
  border: #fc9b04 1px solid;
  border-radius: 10px;
  color: #fc9b04;
  -webkit-transform: skew(-30deg);
          transform: skew(-30deg);
}

ul li {
  list-style-type: none;
}

.quotation {
  margin: 0;
  display: relative;
}

.text {
  height: 300px;
  background: #333;
  text-align: center;
  color: #fff;
}
.text h1 {
  margin-top: 0rem;
  padding-top: 2rem;
  font-size: 40px;
}

.text h2 {
  margin-top: 0rem;
  padding-top: 2rem;
  font-size: 40px;

  display: absolute;
  top: 0;
  left: 0;
}
.text h2 span {
  color: #fc9b04;
}

.howitworks {
  text-align: center;
}
.howitworks h3 {
  font-size: 30px;
}

.howitworks .bottom {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
}

.howitworks .bottom a {
  text-decoration: none;
}

.howitworks .bottom .link1 a {
  background: #fc9b04;
  color: #fff;
  padding: 1rem;
}
.howitworks .bottom .link2 a {
  background: #f05327;
  color: #fff;
  padding: 1rem;
}

.howitworks .bottom p {
  font-size: 20px;
  margin: 1.8rem;
}

.footer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin-top: 2rem;
  background: #333;
  color: #fff;
  justify-items: center;
}

.footer .info {
  padding: 0.5rem;
}

.footer .about li {
  padding-bottom: 0.4rem;
}

.footer .contact li {
  padding-bottom: 0.4rem;
}

